import React, {useEffect, useState} from 'react';
import {changeCurrentAdminMenuLink, metaTitle, refreshPage} from "../../../utils/misc";
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import {useDispatch, useSelector} from "react-redux";
import Spinner from "../../reusable/Loader/Spinner";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import {toast} from "react-toastify";
import {filterByLabel, filterByValue} from "../../../utils/objects";
import {yesOrNoList} from "../../../constants/lists";
import {useForm} from "react-hook-form";
import {saveSettings} from "../../../redux/thunks/saveSettings";
import ReactSelect from "../../reusable/Form/ReactSelect";
import InputNumber from "../../reusable/Form/InputNumber";
import InputText from "../../reusable/Form/InputText";
import useTranslation from "../../../hooks/useTranslation";

const Settings = () => {

    // manage global state
    const {errors: saveErrors, success: saveSuccess, loading: saveLoading} = useSelector(state => state.saveSettingsReducer);
    const {loading, fetched} = useSelector(state => state.fetchSettingsReducer);
    const dispatch = useDispatch();

    // manage local state
    const [isFormSubmitted, setFormSubmitted] = useState(false);

    const availableLanguages = document.globals.available_languages;

    // form & handle form functions

    /**
     *
     * @param key
     * @return {string|boolean}
     */
    const validateGoogleMapsApiKey = (key) => {

        if(typeof key === 'undefined' ||  key === null || key === '' || key.length === 0){
            return  true;
        }

        if(key.length !== 39){
            return useTranslation('Key length must be 39.');
        }

        const regx = new RegExp( "^[A-Za-z0-9-_]+$" );

        if(!regx.test(key)){
            return useTranslation('Not valid format.');
        }

        return true;
    };

    const onSubmit = (data) => {
        dispatch(saveSettings({
            records_per_page: data.records_per_page,
            enable_visual_editor: data.enable_visual_editor ? data.enable_visual_editor.value : null,
            delete_tables_when_deactivate: data.delete_tables_when_deactivate ? data.delete_tables_when_deactivate.value : null,
            delete_posts: data.delete_posts ? data.delete_posts.value : null,
            delete_metadata: data.delete_metadata ? data.delete_metadata.value : null,
            google_maps_api_key: data.google_maps_api_key,
            language: data.language ? data.language.value : null
        }));
        setFormSubmitted(true);
    };

    useEffect(()=>{
        if(isFormSubmitted && saveSuccess){
            toast.success(useTranslation("Settings saved. The browser will refresh after 5 seconds."));
            refreshPage(5000);
        }

        if(isFormSubmitted && saveErrors.length > 0){
            saveErrors.map((error)=>
                toast.error(error)
            );
        }

        if(isFormSubmitted){
            setFormSubmitted(false);
        }
    }, [saveLoading]);

    const enableVisualEditorDefaultValue = (fetched.length > 0 && typeof filterByLabel(fetched, 'key', 'enable_visual_editor') !== 'undefined') ? filterByValue(yesOrNoList, filterByLabel(fetched, 'key', 'enable_visual_editor').value) : {label: useTranslation("Select"), value: null};
    const deletePostsDefaultValue = (fetched.length > 0 && typeof filterByLabel(fetched, 'key', 'delete_posts') !== 'undefined') ? filterByValue(yesOrNoList, filterByLabel(fetched, 'key', 'delete_posts').value) : {label: useTranslation("Select"), value: null};
    const deleteMetadataDefaultValue = (fetched.length > 0 && typeof filterByLabel(fetched, 'key', 'delete_metadata') !== 'undefined') ? filterByValue(yesOrNoList, filterByLabel(fetched, 'key', 'delete_metadata').value) : {label: useTranslation("Select"), value: null};
    const deleteDefinitionsDefaultValue = (fetched.length > 0 && typeof filterByLabel(fetched, 'key', 'delete_tables_when_deactivate') !== 'undefined') ? filterByValue(yesOrNoList, filterByLabel(fetched, 'key', 'delete_tables_when_deactivate').value) : {label: useTranslation("Select"), value: null};
    const perPageDefaultValue = (fetched.length > 0 && filterByLabel(fetched, 'key', 'records_per_page') !== '') ? filterByLabel(fetched, 'key', 'records_per_page').value : 20;
    const googleMapsApiKeyDefaultValue = (fetched.length > 0 && filterByLabel(fetched, 'key', 'google_maps_api_key').value !== '') ? filterByLabel(fetched, 'key', 'google_maps_api_key').value : null;
    const languageDefaultValue = (fetched.length > 0 && typeof filterByLabel(fetched, 'key', 'language').value !== 'undefined') ? filterByValue(availableLanguages, filterByLabel(fetched, 'key', 'language').value) : { value: 'en', label: "English" };

    const { control, register, handleSubmit, formState: {errors, isValid} } = useForm({
        mode: 'all',
        defaultValues: {
            enable_visual_editor: enableVisualEditorDefaultValue,
            delete_metadata: deleteMetadataDefaultValue,
            delete_posts: deletePostsDefaultValue,
            delete_tables_when_deactivate: deleteDefinitionsDefaultValue,
            records_per_page: perPageDefaultValue,
            google_maps_api_key: googleMapsApiKeyDefaultValue,
            language: languageDefaultValue,
        }
    });

    useEffect(() => {
        metaTitle(useTranslation("Settings page"));
        changeCurrentAdminMenuLink('#/settings');
    }, []);

    if(loading){
        return <Spinner/>;
    }

    const submitButton = <button
        className="acpt-btn acpt-btn-primary"
        disabled={(!isValid || loading) ? 'disabled' : ''}
    >
        {loading ? useTranslation('Loading...') : useTranslation('Save')}
    </button>;

    return (
        <Layout>
            <form onSubmit={handleSubmit(onSubmit)}>
                <ActionsBar
                    title="Settings"
                    actions={submitButton}
                />
                <main>
                    <Breadcrumbs crumbs={[
                        {
                            label: useTranslation("Registered Custom Post Types"),
                            link: "/"
                        },
                        {
                            label: useTranslation("Settings")
                        }
                    ]} />
                    <div className="acpt-card">
                        <div className="acpt-card__header">
                            <div className="acpt-card__inner">
                                {useTranslation("Your favourite ACPT settings")}
                            </div>
                        </div>
                        <div className="acpt-card__inner">
                            <fieldset>
                                <legend>
                                    {useTranslation("General settings")}
                                </legend>
                                <ReactSelect
                                    id="language"
                                    label={useTranslation("Language")}
                                    placeholder={useTranslation("Set the language of the plug-in")}
                                    description={useTranslation("Set the language of the plug-in")}
                                    control={control}
                                    defaultValue={languageDefaultValue}
                                    values={availableLanguages}
                                    isRequired={true}
                                    validate={{
                                        required: "This field is mandatory"
                                    }}
                                />
                                <InputNumber
                                    id="records_per_page"
                                    label={useTranslation("Records per page")}
                                    placeholder={useTranslation("Records per page in ACPT dashboards")}
                                    defaultValue={perPageDefaultValue}
                                    description={useTranslation("Set the number of records per page in your ACPT dashboards.")}
                                    register={register}
                                    errors={errors}
                                    isRequired={true}
                                    min={1}
                                    max={200}
                                />
                                <InputText
                                    id="google_maps_api_key"
                                    label={useTranslation("Google Maps API key")}
                                    placeholder={useTranslation("Google Maps API key")}
                                    defaultValue={googleMapsApiKeyDefaultValue}
                                    description={useTranslation("Paste here your Google Maps API key. Needed to use the Address meta field.")}
                                    register={register}
                                    errors={errors}
                                    isRequired={false}
                                    validate={{
                                        validate: validateGoogleMapsApiKey
                                    }}
                                />
                            </fieldset>
                            <fieldset>
                                <legend>
                                    {useTranslation("Content settings")}
                                </legend>
                                <ReactSelect
                                    id="enable_visual_editor"
                                    label={useTranslation("Enable ACPT visual builder")}
                                    placeholder={useTranslation("Enable the ACPT visual builder to manage your templates.")}
                                    description={useTranslation("Enable the ACPT visual builder. This option allows you to use the integrated visual builder to create and manage templates for single CPTs, archive pages, taxonomy terms and relations.")}
                                    control={control}
                                    defaultValue={enableVisualEditorDefaultValue}
                                    values={yesOrNoList}
                                    isRequired={false}
                                />
                            </fieldset>
                            <fieldset>
                                <legend>
                                    {useTranslation("Danger zone")}
                                </legend>
                                <ReactSelect
                                    id="delete_tables_when_deactivate"
                                    label={useTranslation("Delete ACPT definitions when deactivate the plug-in")}
                                    placeholder={useTranslation("Delete ACPT definitions when deactivate the plug-in")}
                                    description={useTranslation("Delete all saved ACPT definitions when you deactivate the plug-in. This means that if you select NO when you deactivate and then reactivate the plug-in you will find all the previously saved ACPT definitions (meta fields, options etc.)")}
                                    control={control}
                                    defaultValue={deleteDefinitionsDefaultValue}
                                    values={yesOrNoList}
                                    isRequired={false}
                                />
                                <ReactSelect
                                    id="delete_posts"
                                    label={useTranslation("Delete posts when delete an ACPT post definition")}
                                    placeholder={useTranslation("Delete posts when delete an ACPT post definition")}
                                    description={useTranslation("Delete posts when delete an ACPT post type definition. This means that if you select YES, when you delete an ACPT post type definition all the saved posts will be deleted")}
                                    control={control}
                                    defaultValue={deletePostsDefaultValue}
                                    values={yesOrNoList}
                                    isRequired={false}
                                />
                                <ReactSelect
                                    id="delete_metadata"
                                    label={useTranslation("Delete metadata when delete an ACPT field")}
                                    placeholder={useTranslation("Delete post metadata when deleting ACPT fields")}
                                    description={useTranslation("Delete post metadata when deleting an ACPT field. This means that if you select YES, when you delete an ACPT meta field all the saved metadata will be deleted")}
                                    control={control}
                                    defaultValue={deleteMetadataDefaultValue}
                                    values={yesOrNoList}
                                    isRequired={false}
                                />
                            </fieldset>
                        </div>
                    </div>
                </main>
            </form>
        </Layout>
    );
};

export default Settings;